import Vue from 'vue'
import Router from 'vue-router'




Vue.use(Router)

const routes = [

{
  path:'/',
  component:()=>import('./../../src/layoutes/admin/AdminLayout.vue'),
  children:[
    {
      path: '',
      name: "Dashboard",
      component: () => import('../pages/Admin/Dashboard.vue'),
       meta: {
            requiresAuth: true
          }
      
    },

    {
      path:'admin/user-data-list',
      name:'UserDataList',
      component:()=> import('./../pages/Admin/UserDataList.vue')
    },
    {
      path:'admin/user-add',
      name:'AddUser',
      component:()=> import('./../components/Admin/AddUser.vue')
    },
    {
      path:'/admin/edit/user/:id',
      name:'EditUser',
      component:()=> import('./../components/Admin/EditUser.vue')
    }
  ]
},
  {
      path:'/login',
      name:"Adminlogin",
      component: () => import('./../components/Admin/Adminlogin.vue')
    },
    {
      path:'*',
      name:'ErrorPage',
      component:()=> import('./../pages/Admin/ErrorPage.vue')
    }


]






export default new Router({

  routes
})



